import React from "react";
import {Col, Container, Row} from "react-bootstrap";
import './header.css'
import {ABOUT, CONTACT, HOME, PARTNER} from "../../constants/constants";
const Header = (props) => {
  return (
      <Container fluid className="header-container">
        <Row>
          <Col>
            <p className={props.tab === HOME ? 'active-tab' : 'inactive-tab'}>Home</p>
          </Col>
          <Col>
            <p className={props.tab === ABOUT ? 'active-tab' : 'inactive-tab'}>About</p>
          </Col>
          <Col>
            <p className={props.tab === PARTNER ? 'active-tab' : 'inactive-tab'}>Partner</p>
          </Col>
          <Col>
            <p className={props.tab === CONTACT ? 'active-tab' : 'inactive-tab'}>Contact</p>
          </Col>
        </Row>
      </Container>
  )
};

export default Header;
